.main {
  position: relative;
  padding: 50px 0 0 0;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -29px;
    transform: translate(-50%, 0);
    width: 63px;
    height: 63px;
    background: #fff;
    border-radius: 50%;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50px;
    padding: 18px 30px 100px;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 1064px;
      height: 574px;
      background: #4D7BFF;
      filter: blur(131.2px);
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__logo {
    font-family: 'Prompt';
    font-size: 40px;
    line-height: 150%;
    font-weight: 600;
    letter-spacing: -0.8px;
  }
  &__title {
    max-width: 982px;
    font-family: 'Prompt';
    font-size: 62px;
    line-height: 150%;
    font-weight: 600;
    letter-spacing: -1.24px;
    color: #fff;
    margin-top: 38px;
    span {
      background-image: url("../img/line.svg");
      background-repeat: no-repeat;
      background-position: top 10px center;
      background-size: auto;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 116%;
    letter-spacing: -0.36px;
    color: #fff;
    margin-top: 20px;
  }
  &__btn {
    height: 66px;
    border-radius: 11px;
    padding: 5px 40px;
    margin-top: 40px;
    span {
      font-size: 18px;
      letter-spacing: -0.36px;
    }
  }
  &__btn2 {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: -20px;
    transform: translate(-50%, 0);
    width: 43px;
    height: 43px;
    background: #F2F2F2;
    border-radius: 50%;
  }
}
.finance {
  padding: 100px 0 50px;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 40px;
  }
}
.finance-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  &__desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 15px;
  }
  &__icon {
    width: 38px;
    margin-bottom: 15px;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
    letter-spacing: -0.3px;
    color: #737373;
  }
  &__img {
    img {
      width: 100%;
      border-radius: 50px;
    }
  }
}
.connection {
  position: relative;
  padding: 50px 0;
  &__inner {
    position: relative;
  }
  &__title {
    text-align: center;
    span {
      color: #4C6FED;
    }
  }
  &__img {
    position: absolute;
    top: 140px;
    right: 100px;
    width: 166px;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    right: 190px;
    bottom: 0;
    width: 382px;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    top: 180px;
    left: 100px;
    width: 118px;
    img {
      width: 100%;
    }
  }
  &__img4 {
    position: absolute;
    left: 190px;
    bottom: 0;
    width: 382px;
    img {
      width: 100%;
    }
  }
}
.connection-form {
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
  margin-top: 20px;
  &__wrap {
    position: relative;
    z-index: 2;
    max-width: 536px;
    background: linear-gradient(180.00deg, rgb(80, 114, 238),rgb(58, 96, 234) 100%);
    border-radius: 50px;
    padding: 43px 73px;
    margin: 30px auto 0;
  }
  &__title {
    text-align: center;
    font-family: 'Prompt';
    font-size: 24px;
    line-height: 150%;
    letter-spacing: -0.48px;
    color: #E4E9FC;
  }
  &__group {
    input {
      width: 100%;
      height: 60px;
      background: #fff;
      border: 1px solid transparent;
      border-radius: 15px;
      padding: 0 30px;
      font-size: 16px;
      line-height: 112%;
      letter-spacing: -0.32px;
      color: #222;
      &::placeholder {
        color: #9E9E9E;
      }
    }
    .text-danger {
      display: block;
      font-size: 16px;
      line-height: 112%;
      letter-spacing: -0.32px;
      color: #ff0000;
      margin-top: 5px;
    }
  }
  &__btn {
    width: 100%;
    height: 66px;
    span {
      font-size: 18px;
    }
  }
}


@media screen and (max-width: 1700px) {
  .connection {
    &__img2 {
      right: 15px;
    }
    &__img4 {
      left: 15px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .connection {
    &__img2 {
      width: 250px;
    }
    &__img4 {
      width: 250px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .connection {
    &__img {
      right: -30px;
    }
    &__img3 {
      left: -30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding-top: 30px;
    &__logo {
      font-size: 30px;
      letter-spacing: -0.6px;
    }
    &__title {
      font-size: 40px;
      letter-spacing: -0.78px;
      margin-top: 20px;
      span {
        background-image: unset;
        border-bottom: 2px dashed #222;
      }
    }
  }
  .finance {
    padding: 60px 0 30px;
    &__cards {
      margin-top: 20px;
    }
  }
  .finance-card {
    &__img {
      img {
        border-radius: 30px;
      }
    }
  }
  .connection {
    padding: 30px 0;
  }
  .connection-form {
    &__wrap {
      max-width: 450px;
      border-radius: 30px;
      padding: 40px 20px;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .finance {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .connection {
    &__img {
      right: 0;
      width: 100px;
    }
    &__img2,
    &__img4 {
      display: none;
    }
    &__img3 {
      left: 0;
      width: 70px;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__inner {
      border-radius: 30px;
      padding: 18px 15px 80px;
    }
    &__logo {
      font-size: 24px;
      letter-spacing: -0.48px;
    }
    &__title {
      font-size: 30px;
      letter-spacing: -0.6px;
      margin-top: 15px;
    }
    &__subtitle {
      font-size: 16px;
      margin-top: 15px;
    }
    &__btn {
      height: 56px;
      padding: 5px 40px;
      span {
        font-size: 16px;
      }
    }
  }
  .finance {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
      gap: 12px;
    }
  }
  .finance-card {
    &__text {
      font-size: 14px;
      letter-spacing: -0.28px;
    }
  }
  .connection-form {
    &__group {
      input {
        height: 50px;
      }
    }
    &__btn {
      height: 56px;
    }
  }
}