.footer {
  padding-bottom: 50px;
  &__inner {
    background: #222;
    border-radius: 50px;
    padding: 40px 100px 80px;
  }
  &__logo {
    font-family: 'Prompt';
    font-size: 40px;
    line-height: 150%;
    font-weight: 600;
    letter-spacing: -0.8px;
    color: #969696;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
    margin-top: 24px;
  }
  &__title {
    max-width: 540px;
    color: #fff;
    span {
      color: #4C6FED;
    }
  }
  &__group {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn {
    height: 66px;
    gap: 10px;
    background: #4C6FED;
    border-radius: 11px;
    padding: 5px 67px;
    color: #fff;
    &:hover {
      background: #0939ff;
    }
    span {
      font-size: 18px;
      line-height: 116px;
      letter-spacing: -0.36px;
    }
  }
  &__email {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #fff;
    margin-top: 35px;
    &:hover {
      color: #4C6FED;
    }
    i {
      width: 20px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 24px;
      line-height: 140%;
      font-weight: 500;
    }
  }
  &__link {
    font-size: 16px;
    line-height: 140%;
    color: #9E9E9E;
    text-decoration: underline;
    text-underline-offset: 3px;
    margin-top: 35px;
    &:hover {
      color: #4C6FED;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding-bottom: 30px;
    &__inner {
      padding: 30px 30px 60px;
    }
    &__logo {
      font-size: 30px;
      letter-spacing: -0.6px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__wrap {
      flex-direction: column;
      gap: 20px;
      margin-top: 20px;
    }
    &__group {
      align-items: start;
    }
    &__email {
      margin-top: 20px;
      span {
        font-size: 18px;
        letter-spacing: -0.32px;
      }
    }
    &__link {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__inner {
      border-radius: 30px;
      padding: 30px 15px 60px;
    }
    &__logo {
      font-size: 24px;
      letter-spacing: -0.48px;
    }
    &__btn {
      height: 56px;
      padding: 5px 40px;
      span {
        font-size: 16px;
      }
    }
  }
}