.wrapper {
  overflow: hidden;
}
h2 {
  font-family: 'Prompt';
  font-size: 40px;
  line-height: 150%;
  font-weight: 600;
  letter-spacing: -0.8px;
}
.btn-black {
  gap: 10px;
  background: #222;
  border-radius: 11px;
  color: #fff;
  &:hover {
    background: #000;
  }
  span {
    line-height: 116%;
  }
  i {
   max-width: 24px;
  }
  i svg {
    max-width: 100%;
    width: auto;
    height: auto;
  }
}

@media screen and (max-width: 992px) {
  h2 {
    font-size: 30px;
    letter-spacing: -0.6px;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 24px;
    letter-spacing: -0.48px;
  }
}